import React, {useState, useEffect} from 'react';
import { Modal, Icon, Button, Image } from 'semantic-ui-react';
import { observer } from 'mobx-react-lite';
import { fetchApp } from '../../http/appApi';
import { REACT_APP_API_STATIC } from '../../utils/consts';
import AppLoading from '../../components/AppLoading';

let AppModal = observer(({isOpen, appInfo, setModal})=>{
    let [appLoadingStatus, setAppLoading] = useState(false);
    let closeHendler = ()=>{
        setModal(false,appInfo);
    };
    useEffect(()=>{
        /*if(!appInfo.app_description && !appInfo.app_link){
            fetchApp(appInfo.app_id).then(data=>{
                appInfo = Object.assign(appInfo, data);
                setAppLoading(false);
            });
        } else setAppLoading(false);*/
    },[]);
    return(
        <>
            {appLoadingStatus && <AppLoading/>}
            {!appLoadingStatus && 
                <Modal
                    size='tiny'
                    centered={false}
                    open={isOpen}
                    onClose={closeHendler}
                >
                    <Modal.Header>Приложение : {appInfo.app_name}</Modal.Header>
                    <Modal.Content>
                        <Modal.Description>
                            <Image src={REACT_APP_API_STATIC+appInfo.app_image} size='small' rounded style={{'margin' : '0 auto'}}/>
                            <div>
                                {appInfo.app_description}
                            </div>
                            <div style={{'marginTop' : '10px'}}>
                                <a href={appInfo.app_link}>
                                    <Icon name='external alternate'/>
                                    Перейти на сайт приложения
                                </a>
                            </div>
                        </Modal.Description>
                    </Modal.Content>
                    <Modal.Actions>
                        <Button onClick={closeHendler}>
                            Закрыть
                        </Button>
                        <Button onClick={closeHendler} positive>
                            <Icon name='arrow alternate circle down outline' />Установить
                        </Button>
                    </Modal.Actions>
                </Modal>
            }
        </>
    );
});

export default AppModal;