import React from 'react';
import { Card, Image } from 'semantic-ui-react';
import {REACT_APP_API_STATIC} from '../utils/consts';

let AppItem = ({info})=>{
    let {app_name, app_image} = info;
    return(
        <Card style={{'width' : '150px'}} link={true}>
            <Image src={REACT_APP_API_STATIC+app_image} wrapped ui={false}/>
            <Card.Content textAlign={'center'}>
                <Card.Header>{app_name}</Card.Header>
            </Card.Content>
        </Card>
    );
};

export default AppItem;