import React, {useEffect, useState} from 'react';
import AppItem from '../components/AppItem';
import AppModal from '../components/Modals/AppModal';
import { Image, Dimmer, Loader, Icon, List, Card, Modal, Button } from 'semantic-ui-react';
import {observer} from 'mobx-react-lite';
import { fetchApps } from '../http/appApi';

let Main = observer(()=>{
    let [appList, setAppList] = useState([
        {
            app_id : 1,
            app_name : 'iTunes',
            app_image : 'itunes.png',
            app_description : 'itunes app',
            app_link : 'https://itunesconnect.apple.com/'
        },
        {
            app_id : 2,
            app_name : 'iTunes',
            app_image : 'itunes.png',
            app_description : 'itunes app',
            app_link : 'https://itunesconnect.apple.com/'
        },
        {
            app_id : 3,
            app_name : 'iTunes',
            app_image : 'itunes.png',
            app_description : 'itunes app',
            app_link : 'https://itunesconnect.apple.com/'
        }
    ]);
    let [modalOpend, setModal] = useState(false);
    let [currentIndex, setIndex] = useState(null);
    let modalHendler = (status,index)=>{
        setModal(status);
        setIndex(index);
    };

    let closeModalHendler = (status, newData)=>{
        setModal(status);
        appList[currentIndex] = newData;
        setAppList(appList);
    }

    useEffect(()=>{
        /*fetchApps().then(data=>{
            setAppList(data);
        });*/
    },[]);

    return(
        <>
            {modalOpend && <AppModal
                isOpen={modalOpend}
                setModal = {(status,id)=>closeModalHendler(status,id)}
                appInfo={appList[currentIndex]}
            />}
            <List horizontal style={{width : '100%'}}>
                {appList.map((data, index)=>{
                    return <List.Item key={index} onClick={()=>{modalHendler(true, index)}}>
                        <AppItem info={data}/>
                    </List.Item>
                })}
            </List>
        </>
    );
});

export default Main;